  border-bottom: 1px solid rgb(226, 98, 24) !important;
  -webkit-box-shadow: 0 1px 0 0 rgb(226, 98, 24) !important;
          box-shadow: 0 1px 0 0 rgb(226, 98, 24) !important;
}

.gallery{
  display: -ms-flexbox;
  display: flex;
 -ms-flex-wrap: wrap;
     flex-wrap: wrap;
 -ms-flex-pack: distribute;
     justify-content: space-around;  
}
